import React, { Component } from "react";
import renderControl from "./helpers/renderControl";

const render = (control) => renderControl({ content: control });

const MobileButton = (props) => {
  const { button, setActiveMenu, additionalClassNames } = props;

  button.additionalClassNames = additionalClassNames;
  button.onClick = () => setActiveMenu();

  return render(button);
};

const classDisplay = (thisIndex, actualIndex) => {
  return thisIndex !== actualIndex
    ? { className: "is-hidden" }
    : { className: "" };
};

export class MobileCore extends Component {
  constructor(props) {
    super(props);

    this.state = {
      active: null,
    };
  }

  render() {
    const { layout } = this.props;
    const { active } = this.state;

    return (
      <>
        <div
          className={`mobile-core__general-content ${
            classDisplay(null, active).className
          }`}
        >
          {render(layout.generalContent)}
        </div>
        <div
          className={`mobile-core__menus ${
            classDisplay(null, active).className
          }`}
        >
          {layout.menus.map((menu, index) => (
            <div
              key={index}
              className={`mobile-core__menu ${
                classDisplay(index, active).className
              }`}
            >
              {render(menu)}
            </div>
          ))}
        </div>
        <div className="mobile-core__buttons container">
          {layout.buttons.map((button, index) => (
            <MobileButton
              key={index}
              button={button}
              additionalClassNames={
                active === index ? "button--highlight" : null
              }
              setActiveMenu={() =>
                this.setState({ active: active === index ? null : index })
              }
            />
          ))}
        </div>
      </>
    );
  }
}
