import { combineReducers } from "redux";
import { createReducer } from "dragonglass-redux";
import { bindToMap } from "../reduxHelper";
import initialState from "../initialState.js";
import {
    DRAGONGLASS_VIEW_DEFINE,
    DRAGONGLASS_VIEW_RESET,
    DRAGONGLASS_VIEW_SET_ACTIVE
} from "../actions/viewActionTypes.js";
import { setActiveViewAction } from "../actions/viewActions.js";

const active = createReducer("", {
    [DRAGONGLASS_VIEW_SET_ACTIVE]: (state, payload) => state === payload ? state : payload
});

const views = createReducer(initialState.view.views, {
    [DRAGONGLASS_VIEW_DEFINE]: (state, payload) => {
        const { tag } = payload;
        if (state.layouts[tag] && state.layouts[tag] === payload)
            return state;

        const views = [...new Set([...state.tags, tag])];
        return Object.assign({}, state, { layouts: { ...state.layouts, [tag]: { ...payload } }, tags: views });
    },
    [DRAGONGLASS_VIEW_RESET]: () => initialState.view,
});

export default combineReducers({
    active,
    views
});

const layoutMap = {
    state: (state, ownProps) => {
        const { layouts } = state.view.views, { tag } = ownProps;
        return { layout: layouts.hasOwnProperty(tag) ? layouts[tag] : {} };
    },
    enhancer: {
        areStatesEqual: (next, prev) => {
            if (next.view.views.layouts !== prev.view.views.layouts)
                return false;

            if (Object.keys(next.view.views.layouts).length !== Object.keys(prev.view.views.layouts).length)
                return false;

            for (let key in next.view.views.layouts) {
                if (next.view.views.layouts.hasOwnProperty(key))
                    if (next.view.views.layouts[key] !== prev.view.views.layouts[key])
                        return false;
            }

            return true;
        }
    }
};

const activeMap = {
    state: (state, ownProps) => {
        const { view } = state;
        return { active: view.active === ownProps.tag };
    },
    enhancer: {
        areStatesEqual: (next, prev) => next.view.active === prev.view.active
    }
};

const tagMap = {
    state: state => ({ tags: state.view.views.tags }),
    enhancer: {
        areStatesEqual: (next, prev) => next.view.views.tags === prev.view.views.tags
    }
};

const viewMap = {
    state: state => ({
        view: state.view
    }),
    enhancer: {
        areStatesEqual: (next, prev) =>
            next.view.active === prev.view.active && next.view.views.tags === prev.view.views.tags
    },
    dispatch: dispatch => ({
        setActiveView: (tag) => dispatch(setActiveViewAction(tag))
    })
}

export const bindComponentToViewLayoutState = component => bindToMap(component, layoutMap);
export const bindComponentToViewActiveState = component => bindToMap(component, activeMap);
export const bindComponentToViewTagsState = component => bindToMap(component, tagMap);
export const bindComponentToViewState = component => bindToMap(component, viewMap);