import React, { Component } from "react";
import scrollIntoView from "scroll-into-view-if-needed";
import ViewEventHost from "../EventHosts/ViewEventHost";
import { bindComponentToDataSetRepeaterCurrentRowState } from "../../redux/reducers/dataReducer";
import { DataType } from "../../enums/DataType";

const Columns = ({ columns, row, showSelectColumn, active, toggleSelection }) => {
    return <>
        {
            columns.filter(column => column.visible).map((column, index) => {
                let value = row.fields && row.fields[column.fieldId];
                value = (row.pending && !value)
                    ? value = <span></span>
                    : DataType.behavior[column.dataType].format(value);
                return (
                    <td className={row.pending ? "loading" : ""} key={column.fieldId}>
                        {
                            showSelectColumn && index === 0
                                ? <input type="checkbox" checked={active} onChange={toggleSelection} />
                                : null
                        }
                        {value}
                    </td>
                )
            })
        }
    </>
};

let lastScrollIntoView = 0;

class GridRow extends Component {
    constructor(props) {
        super(props);
        this.refTr = React.createRef();
    }

    componentDidMount() {
        this.scrollIntoViewIfNeeded();
    }

    shouldComponentUpdate(nextProp) {
        return nextProp.active !== this.props.active || nextProp.row !== this.props.row;
    }

    scrollIntoViewIfNeeded(instant) {
        let thisScrollIntoView = ++lastScrollIntoView;

        setTimeout(() => {
            if (thisScrollIntoView !== lastScrollIntoView)
                return;
            scrollIntoView(this.refTr.current, { behavior: instant ? "instant" : "smooth", scrollMode: "if-needed" });
        });
    }

    render() {
        const { active, row, showSelectColumn } = this.props;
        const { columns } = this.props.dataSource;
        const additional = {};
        active && (additional.className = "tr-highlight");
        return (
            <tr
                {...additional}
                ref={this.refTr}
                onClick={this.props.setActive}
            >
                <Columns
                    columns={columns}
                    row={row}
                    showSelectColumn={showSelectColumn}
                    toggleSelection={this.props.toggleSelection}
                    active={active}
                />
                <ViewEventHost eventHandlers={{
                    onChangeActiveView: (newView) =>
                        setTimeout(() => this.props.active && this.scrollIntoViewIfNeeded(true))
                }} />
            </tr>
        )
    }
}

export default bindComponentToDataSetRepeaterCurrentRowState(GridRow);
