import React, { PureComponent } from "react";
import renderControl from "./helpers/renderControl";
import Layout from "./Layout";
import { bindComponentToViewLayoutState } from "../redux/reducers/viewReducer";
import { Workflow } from "dragonglass-workflows";

class View extends PureComponent {
  render() {
    const { tag, active, layout } = this.props;

    if (active) {
      Workflow.definePerViewWorkflowSetup(layout.workflows || {}, tag);
    }

    return (
      <Layout id={`view-${tag}`} className={`view ${tag}`} layout={layout}>
        {renderControl(layout)}
      </Layout>
    );
  }
}

export default bindComponentToViewLayoutState(View);
