export class CloseInfo {
    Code: number;
    Reason: string;
    Error: boolean;

    constructor(event: any) {
        this.Code = event.code;
        this.Reason = event.reason;
        this.Error = !event.wasClean;
    }

    getReason(): string {
        return `${(this.Error ? "error" : "reason")} (${this.Code}) ${this.Reason}`;
    }
}
