import React, { Component } from "react";
import "./styles/index.scss";
import Page from "./components/Page";
import { bindComponentToViewTagsState } from "./redux/reducers/viewReducer";
import { DragonglassPage } from "./dragonglass-ui/controls/DragonglassPage";
import { DragonglassManager } from "./DragonglassManager";

window.focus();

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ui: "transcendence",
    };
    this.props.uiSwitcher.switchUi = () => this.setState({ ui: "🐲🍸" });
  }

  render() {
    return (
      <div
        className={`body super-ux ${
          this.state.ui === "transcendence" ? "transcendence" : "dragonglass"
        }`}
      >
        <DragonglassManager />
        {this.state.ui === "transcendence" ? (
          this.props.tags.map((tag) => <Page tag={tag} key={tag}></Page>)
        ) : (
          <DragonglassPage></DragonglassPage>
        )}
      </div>
    );
  }
}

export default bindComponentToViewTagsState(App);
