import { createReducer } from "dragonglass-redux";
import { bindToMap } from "../reduxHelper";
import initialState from "../initialState.js";
import { DRAGONGLASS_MENU_DEFINE, DRAGONGLASS_MENU_RESET } from "../actions/menuActionTypes";
import { getButtonGridMenu } from "../../classes/ButtonGridMenuBuilder.js";

const menus = createReducer(initialState.menus, {
    [DRAGONGLASS_MENU_DEFINE]: (state, payload) => {
        const newState = { ...state, ...payload };
        newState._generation++;
        for (var key in payload)
            newState[key].generation = ((state[key] && state[key].generation) || 0) + 1;
        return newState;
    },

    [DRAGONGLASS_MENU_RESET]: state => {
        const newState = { ...state };
        newState._generation++;

        for (let key in state) {
            if (typeof newState[key] === "object" && typeof newState[key].generation === "number")
                newState[key].generation++;
        }
        
        return newState;
    }
});

export default menus;

const menuStateMap = {
    state: (state, ownProps) => {
        const { menu } = state;
        const layout = ownProps.layout || {};
        const id = layout && layout.source || "";
        return { menu: getButtonGridMenu(menu[id] || { MenuButtons: [], generation: 0 }, layout.rows, layout.columns, state.transactionState) }
    },
    enhancer: {
        areStatesEqual: (next, prev) => next.menu._generation === prev.menu._generation
    }
};

export const bindComponentToMenuState = component => bindToMap(component, menuStateMap);
