//This script is currently embedded in a NAV codeunit, as minified javascript in a string.

import { AckMessage } from "./AckMessage";
import { HandlerMessage } from "./HandlerMessage";
import { ResponseCallback } from "./ResponseCallback";
import { CloseInfo } from "./CloseInfo";

export class HardwareConnector {
    private socket: WebSocket;
    private lastMessageIdSend: number;
    private responseCallbacks: Array<ResponseCallback>;
    private closeInfo: CloseInfo;

    constructor() {
        this.setupObject();
    }

    async sendRequestAndWaitForResponseAsync(
        handler: string,
        content: any
    ): Promise<any> {
        let message = new HandlerMessage(
            ++this.lastMessageIdSend,
            this.generateContextID(),
            handler,
            content
        );

        if (this.socket.readyState !== this.socket.OPEN) {
            await this.socketConnectionDoneAsync();
        }

        let responseResult = this.waitForFirstResponse(message);
        let sendResult = this.sendRequestAndWaitForAckAsync(message);

        return (await Promise.all([sendResult, responseResult]))[1].HandlerContent;
    }

    async sendRequestAsync(
        handler: string,
        content: any,
        context: string
    ): Promise<void> {
        let message = new HandlerMessage(
            ++this.lastMessageIdSend,
            context,
            handler,
            content
        );

        if (this.socket.readyState !== this.socket.OPEN) {
            await this.socketConnectionDoneAsync();
        }

        return await this.sendRequestAndWaitForAckAsync(message);
    }

    registerResponseHandler(callback: (HandlerContent: any) => void): string {
        let context = this.generateContextID();
        this.responseCallbacks.push(new ResponseCallback(context, callback));
        return context;
    }

    unregisterResponseHandler(context: string) {
        this.responseCallbacks = this.responseCallbacks.filter(
            value => value.context !== context
        );
    }

    getSocketState() {
        return this.socket.readyState;
    }

    private getSocketClosedError() {
        return new Error(`[HardwareConnector] Socket is closed with ${this.closeInfo.getReason()}`);
    }

    private async socketConnectionDoneAsync() {
        return new Promise((resolve, reject) => {
            switch (this.socket.readyState) {
                case this.socket.OPEN:
                    resolve();
                    return;
                case this.socket.CLOSED:
                    reject(this.getSocketClosedError());
                    return;
                case this.socket.CLOSING:
                    reject(new Error("[HardwareConnector] Socket is closing, connection is not possible."));
                    return;
            }

            this.socket.addEventListener("close", () => reject(this.getSocketClosedError()));
            this.socket.addEventListener("open", () => resolve());
        });
    }

    private async sendRequestAndWaitForAckAsync(
        message: HandlerMessage
    ): Promise<void> {
        return new Promise((resolve, reject) => {
            this.socket.addEventListener("close", event => {
                clearInterval(intervalId);
                reject(event);
            });
            this.socket.addEventListener("message", event => {
                let response = JSON.parse(event.data) as AckMessage;
                if (
                    response.Type === "Acknowledgement" &&
                    response.AcknowledgeId === message.Id
                ) {
                    clearInterval(intervalId);
                    resolve();
                }
            });

            let rawMessage = JSON.stringify(message);

            //Repeat message every 100 milliseconds until ack is received.
            let intervalId = setInterval(() => {
                this.socket.send(rawMessage);
                console.log(
                    "[HardwareConnector] Data sent to localhost: " + rawMessage
                );
            }, 100);
        });
    }

    private async waitForFirstResponse(
        message: HandlerMessage
    ): Promise<HandlerMessage> {
        return new Promise((resolve, reject) => {
            this.socket.addEventListener("close", event => reject(event));
            this.socket.addEventListener("message", event => {
                let response = JSON.parse(event.data) as HandlerMessage;
                if (
                    response.Type === "Handler" &&
                    response.Context === message.Context
                ) {
                    resolve(response);
                }
            });
        });
    }

    private handleMessage(rawMessage: any) {
        console.log(
            `[HardwareConnector] Data received from localhost: ${rawMessage.data}`
        );

        let message = JSON.parse(rawMessage.data) as HandlerMessage;

        if (message.Type === "Handler") {
            let ack = JSON.stringify(
                new AckMessage(++this.lastMessageIdSend, message.Id)
            );
            this.socket.send(ack);
            console.log("[HardwareConnector] Data sent to localhost: " + ack);

            this.responseCallbacks
                .filter(
                    value =>
                        value.context === message.Context && value.lastId < message.Id
                )
                .forEach(value => {
                    value.lastId = message.Id;
                    console.log(
                        "[HardwareConnector] Invoking registered response handler for context: " +
                        message.Context
                    );
                    value.callback(message.HandlerContent);
                });
        }
    }

    private handleClose(event: any) {
        this.closeInfo = new CloseInfo(event);
        if (event.wasClean) {
            console.log(
                `[HardwareConnector] Connection with localhost closed, code=${
                event.code
                } reason=${event.reason}`
            );
        } else {
            console.log(
                "[HardwareConnector] Connection with localhost died unexpectedly"
            );
        }

        /*
        setTimeout(() => {
          if (this.socket.readyState !== this.socket.OPEN) {
            this.setupObject();
          }
        }, 2000);
        */
    }

    private generateContextID(): string {
        return crypto.getRandomValues(new Uint32Array(4)).join("");
    }

    private setupObject() {
        this.lastMessageIdSend = 0;
        this.responseCallbacks = [];
        this.socket = new WebSocket("ws://127.0.0.1:60992"); //Hardcoded port in both ends

        this.socket.addEventListener("open", () =>
            console.log("[HardwareConnector] Connection established with localhost")
        );
        this.socket.addEventListener("error", error =>
            console.log(`[HardwareConnector] Error: ${error}`)
        );
        this.socket.addEventListener("message", event => this.handleMessage(event));
        this.socket.addEventListener("close", event => this.handleClose(event));
    }
}
