import { isMobile } from "../../classes/functions";

const hasEntryWithClass = (content, className) => !!content.find(entry => entry.class === className)

export const transformForMobileIfNeeded = layout => {
    if (!isMobile())
        return layout;

    const { content, ...other } = layout;

    if (!content || typeof content !== "object")
        return other;

    if (!Array.isArray(content)) {
        other.content = content;
        return other;
    }

    other.content = [];

    if (hasEntryWithClass(content, "togglable") && hasEntryWithClass(content, "more-menu") && hasEntryWithClass(content, "items-menu") && hasEntryWithClass(content, "bottom-menu")) {
        const mobileCore = {
            type: "mobilecore",
            generalContent: null,
            menus: [null, null],
            buttons: []
        };
        content.forEach(entry => {
            switch (entry.class) {
                case "togglable":
                    mobileCore.generalContent = entry;
                    return;
                case "bottom-menu":
                    mobileCore.buttons = entry.content;
                    return;
                case "items-menu":
                    mobileCore.menus[0] = entry;
                    return;
                case "more-menu":
                    mobileCore.menus[1] = entry;
                    return;
                default:
                    other.content.push(entry);
                    return;
            }
        });
        other.content.push(mobileCore);
    } else {
        content.forEach(entry => other.content.push(transformForMobileIfNeeded(entry)));
    }

    return other;
};
