export class HandlerMessage {
    Type: string;
    Id: number;
    Context: string;
    Handler: string;
    HandlerContent: any;

    constructor(
        id: number,
        context: string,
        handler: string,
        handlerContent: any
    ) {
        this.Type = "Handler";
        this.Id = id;
        this.Context = context;
        this.Handler = handler;
        this.HandlerContent = handlerContent;
    }
}
